.pack{
    text-align:left;
    padding:10%;
    margin:2%;
}
.packName{
    display:inline-block;
}
.buttonWrapper{
    float:right;
    width:25%;
    margin-top:-2%;
}
.pillToggle{
    text-align: right;
    border:solid tan 2px;
    width:100%;
    margin-top:2%;
    margin-bottom:2%;
    right:0px;
    vertical-align: middle;
    border-radius: 50px;
    padding:0.2%;
    cursor: pointer;
}
.pillToggle:hover{
    box-shadow: gray 0px 0px 5px 0px;
}
.buttonDot{
    position:relative;
    border-radius: 100px;
    width:2.5vh;
    height:2.5vh;
    margin:1%;
    padding:2%;
    text-align: center;
    line-height:2.5vh;
    transition: left 0.09s ease, background-color 0.09s ease-in-out;
}
.off{
    background-color:red;
    color:white;
    left:0;
}

.on{
    background-color:green;
    color:white;
    left:60%;
}