body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    background-color:cornsilk;
}
#preGameDiv{
    text-align: center;
}
#WordsUpload>button, #WordsUpload>form>input,#startButton,#settingsMenu>button ,#menu>button,#game>button,#answer + input[type='submit'],#creditsButton,#settingsButton{
    background-color:tan;
    border:none;
    padding:1%;
    margin:1%;
    width:200px;
    cursor: pointer;
    border-radius:15px;
}
input[type='file']{
    display:none;
}
#game{
    text-align:center;
    width:40%;
    margin:auto
}
#answer{
    padding:1.5%;
    margin:1%;
    width:66%;
    border:solid tan 1px;
    border-radius:15px;
}
.letter{
    
    display:inline-block;
    padding:1%;
    margin:1%;
    height:20px;
    width:20px;
    border-radius:15px;
    font: x-large;
    border:solid black 2px;
}
.clickable{
    cursor:pointer
}
.nonclickable{
    background-color: black;
}
.GuessingFieldLetter{
    display:inline-block;
    width:20px;
    height:20px;
    text-align:center;
    border-bottom:solid red 2px;
    padding:0.5%;
    margin:0.5%;
}
.noUnderline{
    display:inline-block;
    width:20px;
    height:20px;
    padding:0.5%;
    margin:0.5%;
}
#grayout{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:3000px;
    background-color: rgba(0, 0, 0, 0.8);
}
#menu{
    width:40%;
    padding:2%;
    text-align: center;
    background-color:whitesmoke;
    margin:auto;
    margin-top:10px;
}
#settingsMenu{
    width:20%;
    padding:2%;
    text-align: center;
    background-color:whitesmoke;
    margin:auto;
    margin-top:10px;
}
.pack{
    text-align:left;
    padding:10%;
    margin:2%;
}
.packName{
    display:inline-block;
}
.buttonWrapper{
    float:right;
    width:25%;
    margin-top:-2%;
}
.pillToggle{
    text-align: right;
    border:solid tan 2px;
    width:100%;
    margin-top:2%;
    margin-bottom:2%;
    right:0px;
    vertical-align: middle;
    border-radius: 50px;
    padding:0.2%;
    cursor: pointer;
}
.pillToggle:hover{
    box-shadow: gray 0px 0px 5px 0px;
}
.buttonDot{
    position:relative;
    border-radius: 100px;
    width:2.5vh;
    height:2.5vh;
    margin:1%;
    padding:2%;
    text-align: center;
    line-height:2.5vh;
    transition: left 0.09s ease, background-color 0.09s ease-in-out;
}
.off{
    background-color:red;
    color:white;
    left:0;
}

.on{
    background-color:green;
    color:white;
    left:60%;
}
